import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

import 'css-loader';

let lightbox;

export function createGallery(images) {
  const galleryContainer = document.querySelector('.gallery');
  if (!galleryContainer) {
    console.error('–ï–ª–µ–º–µ–Ω—Ç .gallery –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ!');
    return;
  }

  const markup = images.hits
    .map(
      image => `
         <li class="gallery-item">
      <a href="${image.largeImageURL}" class="gallery-link">
        <img src="${image.webformatURL}" alt="${image.tags}" loading="lazy">
      </a>
      <div class="image-info">
        <p>‚ù§Ô∏è ${image.likes}</p>
        <p>üëÅÔ∏è ${image.views}</p>
        <p>üí¨ ${image.comments}</p>
        <p>‚¨áÔ∏è ${image.downloads}</p>
      </div>
    </li>

    `
    )
    .join('');

  galleryContainer.innerHTML = markup;

  if (lightbox) {
    lightbox.refresh();
  } else {
    lightbox = new SimpleLightbox('.gallery a');
  }
}
const gallery = document.querySelector('.gallery');
if (gallery) {
  const images = gallery.querySelectorAll('.gallery-item');
  images.forEach(img => img.remove());
} else {
  console.error('–ï–ª–µ–º–µ–Ω—Ç .gallery –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ!');
}

export function hideLoader() {
  const loader = document.querySelector('.loader');
  if (loader) {
    loader.classList.remove('visible');
  } else {
    console.error('–ï–ª–µ–º–µ–Ω—Ç .loader –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ!');
  }
}

export function showLoader() {
  const loader = document.querySelector('.loader');
  if (loader) {
    loader.classList.add('visible');
  } else {
    console.error('–ï–ª–µ–º–µ–Ω—Ç .loader –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ!');
  }
}

export function clearGallery() {
  const gallery = document.querySelector('.gallery');
  if (gallery) {
    const images = gallery.querySelectorAll('.gallery-item');
    images.forEach(img => img.remove());
  } else {
    console.error('–ï–ª–µ–º–µ–Ω—Ç .gallery –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ!');
  }
}

export default {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
};
